@import "https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&display=swap";
:root {
  --primary-brend: #9bb537;
  --primary-bg-color: #f8f8f8;
  --primary-cards-overlay: linear-gradient(1deg, rgba(5, 5, 5, .6) 0%, rgba(5, 5, 5, 0) 100%);
  --primary-title-color: #050505;
  --primary-text-color: rgba(5, 5, 5, .8);
  --primary-input-title-color: rgba(5, 5, 5, .5);
  --primary-input-text-color: rgba(5, 5, 5, .3);
  --primary-input-border: rgba(5, 5, 5, .2);
  --primary-scroll-bar: rgba(5, 5, 5, .18);
  --rating-stars-active: #eea10c;
  --rating-stars-unactive: #d9d9d9;
  --rating-stars-unactive-cards: rgba(248, 248, 248, .1);
  --rating-stars-unactive-modal: rgba(5, 5, 5, .1);
  --unactive-btn-arrow: rgba(248, 248, 248, .5);
  --primary-switch-btn: #cecdcd;
  --dark-bg-color: #161616;
  --dark-modal-bg: #050505;
  --dark-cards-overlay: linear-gradient(1deg, #050505 0%, rgba(5, 5, 5, 0) 100%);
  --dark-title-color: #fff;
  --dark-text-color: rgba(255, 255, 255, .8);
  --dark-input-title-color: rgba(255, 255, 255, .5);
  --dark-input-text-color: rgba(255, 255, 255, .3);
  --dark-input-border: rgba(255, 255, 255, .2);
  --dark-scroll-bar: rgba(255, 255, 255, .18);
  --dark-rating-stars-unactive: #3c3b3b;
  --dark-rating-stars-unactive-cards: rgba(255, 255, 255, .1);
  --dark-switch-btn: linear-gradient(91deg, #9bb537 0%, rgba(155, 181, 55, .69) 100%);
}

.tablet-desktop {
  display: none;
}

.nav {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo-block {
  margin-top: 28px;
}

.logo-first-color {
  color: var(--primary-brend);
  text-align: center;
  letter-spacing: -.64px;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 18px;
}

.logo-second-color {
  color: var(--primary-title-color);
}

.dark-mode .logo-second-color {
  color: var(--dark-title-color);
}

.active-page, .dark-mode .active-page {
  color: var(--primary-brend);
}

.cart-menu-block {
  align-items: center;
  padding-top: 20.5px;
  display: flex;
}

.dark-mode .header-cart-icon, .dark-mode .header-hamburger-icon {
  fill: var(--dark-title-color);
}

.header-cart-icon:hover, .header-hamburger-icon:hover {
  fill: var(--primary-brend);
  transition: all .3s;
}

.dark-mode .header-cart-icon:hover, .dark-mode .header-hamburger-icon:hover, .dark-mode .close-menu-icon:hover {
  fill: var(--primary-brend);
}

.header-hamburger-icon {
  margin-left: 15px;
}

.mobile-menu {
  width: 200px;
  height: 100%;
  background-color: var(--primary-brend);
  z-index: 11;
  opacity: 0;
  flex-direction: column;
  justify-content: space-between;
  transition: transform .3s, opacity .3s;
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  overflow-y: hidden;
  transform: translateX(100%);
}

.visible {
  opacity: 1;
}

.dark-mode .mobile-menu {
  background-color: var(--dark-mode-bg);
}

.close-menu-icon {
  width: 32px;
  height: 32px;
  fill: var(--dark-title-color);
  position: absolute;
  top: 20px;
  right: 20px;
}

.mobile-menu-nav {
  flex-direction: column;
  align-items: center;
  transition: all .3s;
  display: flex;
  position: absolute;
  top: 200px;
  left: 50%;
  transform: translateX(-50%);
}

.mobile-menu-nav-links {
  color: #fff;
  margin-bottom: 16px;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.mobile-menu-nav-links:last-child {
  margin-bottom: 0;
}

.mobile-menu-nav-links.active-link {
  font-size: 500px;
}

.mobile-menu-nav-links:hover {
  color: #1e1e1e;
}

.dark-mode .mobile-menu-nav-links:hover, .mobile-menu-nav-links.dark-mode-hover:hover {
  color: var(--primary-brend);
}

.mobile-menu-switcher {
  position: absolute;
  bottom: 20px;
  left: 20px;
}

.no-scroll {
  height: 100vh;
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .block-for-mobile, .mobile-menu {
    display: none;
  }

  .tablet-desktop {
    display: block;
  }

  .tablet-desktop-nav {
    justify-content: space-between;
    display: flex;
  }

  .navigations {
    margin-top: 31px;
  }

  .tablet-menu-nav-links {
    font-size: 12px;
    font-weight: 500;
    line-height: normal;
  }

  .tablet-menu-nav-links:not(:last-child) {
    margin-right: 16px;
  }

  .tablet-menu-nav-links:hover {
    color: var(--primary-brend);
    transition: all .3s;
  }

  .tablet-desktop-nav-icons {
    align-items: center;
    margin-top: 20px;
    display: flex;
  }

  .switcher {
    flex-shrink: 0;
    margin-left: 16px;
    margin-right: -22px;
  }

  .logo-first-color, .logo-second-color {
    letter-spacing: -.72px;
    font-size: 18px;
    line-height: 20px;
  }
}

@media screen and (min-width: 1280px) {
  .navigations {
    margin-top: 43px;
  }

  .logo-block {
    margin-top: 40px;
  }

  .tablet-desktop-nav-icons {
    margin-top: 32px;
  }
}



